  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #1e1e1e;
}

.terminal-header {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  background-color: #2d2d2d;
  border-bottom: 1px solid #3e3e42;
  font-size: 13px;
  color: #cccccc;
}

.terminal-body {
  flex: 1;
  padding: 8px;
  overflow: hidden;
}

.terminal-body .xterm {
  height: 100%;
}

.terminal-body .xterm-viewport {
  background-color: #1e1e1e !important;
}


