___CSS_LOADER_EXPORT___.push([module.id, `.file-explorer {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #252526;
  color: #cccccc;
}

.explorer-header {
  padding: 12px 16px;
  background-color: #2d2d30;
  border-bottom: 1px solid #3e3e42;
}

.explorer-header h3 {
  font-size: 11px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: #cccccc;
  margin: 0;
}

.explorer-content {
  flex: 1;
  overflow-y: auto;
  padding: 4px 0;
}

.workspace-folder {
  padding: 8px 16px;
  font-size: 13px;
  font-weight: 600;
  color: #cccccc;
  background-color: #37373d;
  margin-bottom: 4px;
}

.file-tree {
  user-select: none;
}

.file-item {
  display: flex;
  align-items: center;
  padding: 4px 8px;
  cursor: pointer;
  font-size: 13px;
  transition: background-color 0.1s;
}

.file-item:hover {
  background-color: #2a2d2e;
}

.file-item.directory {
  font-weight: 500;
}

.file-item .icon {
  margin-right: 6px;
  font-size: 14px;
}

.file-item .name {
  flex: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.children {
  margin-left: 0;
}

.no-folder {
  padding: 16px;
  text-align: center;
  color: #858585;
  font-size: 13px;
}

.no-folder p {
  margin: 0;
}


